@use '../base/mixins';
@use '../base/variables';

.no-webp .inicio{
    background-image: url(../../../build/img/header.jpg);
}
.webp .inicio{
    background-image: url(../../../build/img/header.webp);
}


.header{
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;
    &.inicio{
        background-position: center center;
        background-size: cover;
        .contenido-header{
            @include tablet{
                min-height: 70rem;
                h1{
                    text-align: left;
                    max-width: 60rem;
                }
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    

    h1{
        color: $blanco;
        font-weight: bold;
        line-height: 2;
        max-width: auto;
    }
}