@include tablet{
    .sobre-nosotros{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 2rem;
        align-items: center;
    }
    
}
.sobre-nosotros blockquote{
    color: $negro;
    margin: 2rem 0 0 0;
    font-weight: $bold;
    font-size: 2.2rem;
}
