body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-verdeBlock {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  display: block;
}
.boton-verdeBlock:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojoBlock {
  background-color: #c52727;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  display: block;
  width: 100%;
}
.boton-rojoBlock:hover {
  background-color: #b02323;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  display: block;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  display: inline-block;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-amarillo_contact {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  display: inline-block;
}
.boton-amarillo_contact:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-rojo {
  background-color: #c52727;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  display: inline-block;
}
.boton-rojo:hover {
  background-color: #b02323;
  cursor: pointer;
}

.dark-mode {
  background-color: #1c1c1c;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode p {
  color: #e1e1e1;
}
.dark-mode .anuncios {
  background-color: #333333;
  border: 1px solid #404040;
}
.dark-mode .anuncios p {
  color: #e1e1e1;
}
.dark-mode .anuncios .precio {
  color: #71B100;
}
.dark-mode .anuncios .iconos-caracteristicas img {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario {
  color: #FFFFFF;
}
.dark-mode .formulario legend,
.dark-mode .formulario label {
  color: #c2c2c2;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario select, .dark-mode .formulario textarea {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}
.dark-mode tbody {
  background-color: #333333;
}
.dark-mode tbody tr {
  color: #FFFFFF;
}
.dark-mode tbody td {
  border-bottom: 1px #4a4a4a solid;
}

/* UI */
/** Media Queries **/
.no-webp .inicio {
  background-image: url(../../../build/img/header.jpg);
}

.webp .inicio {
  background-image: url(../../../build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
  .header.inicio .contenido-header h1 {
    text-align: left;
    max-width: 60rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
  max-width: auto;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  line-height: 1.8rem;
}

@media (min-width: 768px) {
  .navegacion-footer {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}
.navegacion-footer a {
  color: #FFFFFF;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}
.navegacion-footer a:hover {
  color: #71B100;
}

/** Media Queries **/
.derecha {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 2rem;
  align-items: center;
}
.derecha .nav-darkmode {
  filter: invert(80%);
  width: 3rem;
  margin-top: 2rem;
}
.derecha:hover {
  cursor: pointer;
}
@media (min-width: 480px) {
  .derecha {
    flex-direction: column;
    align-items: flex-end;
  }
  .derecha .nav-darkmode {
    margin-bottom: 1rem;
  }
}
@media (min-width: 768px) {
  .derecha {
    margin: 0;
    padding: 1rem;
    display: flex;
    flex-direction: column-reverse;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
}
@media (min-width: 480px) {
  .barra {
    align-items: flex-end;
  }
}
.barra img {
  width: 30rem;
  height: auto;
}
@media (min-width: 768px) {
  .barra {
    margin-top: 5rem;
    padding: 0rem;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.navegacion-header {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.2s linear, height 0.5s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion-header {
    display: flex;
    /* padding: 5rem; */
    gap: 2rem;
    align-items: center;
    visibility: visible;
    opacity: 1;
    height: auto;
    white-space: nowrap;
  }
}
.navegacion-header a {
  color: #FFFFFF;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}
@media (min-width: 480px) {
  .navegacion-header a {
    text-align: right;
  }
}
.navegacion-header a:hover {
  color: #71B100;
}

.mobile-menu {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}
.mobile-menu .logo-img {
  max-width: 40rem;
}
.mobile-menu .mobileMenu-hidden img {
  padding-top: 2rem;
  max-width: 2.6rem;
}
@media (min-width: 480px) {
  .mobile-menu {
    flex-direction: row;
    justify-content: space-between;
  }
  .mobile-menu .mobileMenu-hidden img {
    padding-top: 0;
  }
}
@media (min-width: 768px) {
  .mobile-menu .mobileMenu-hidden {
    display: none;
  }
}

.mostrar {
  display: flex;
  flex-direction: column;
  height: auto;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .mostrar {
    flex-direction: row;
  }
}

/* Seccion */
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncios {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #e1e1e1;
  border: 1px solid #d4d4d4;
  margin-top: 4rem;
}
.anuncios .contenido-anuncios {
  padding: 2rem;
  padding-top: 0;
}
.anuncios .contenido-anuncios h3 {
  margin-top: 2rem;
}
.anuncios h3, .anuncios p {
  margin: 0;
  margin-bottom: 2rem;
}

.precio {
  color: #71B100;
  font-size: 3rem;
  font-weight: 700;
}

.iconos-caracteristicas {
  list-style: none;
  margin-right: 0;
  display: flex;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url(../../../build/img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../../../build/img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.imagen-contacto {
  padding: 10rem 0;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
}
.imagen-contacto p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .grid-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-entrada .parrafo-span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  padding: 2rem;
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 400;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../../../public/build/img/comilla.svg);
  background-repeat: no-repeat;
  width: 5.9rem;
  height: 7rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

/* Paginas Internas */
@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.sobre-nosotros blockquote {
  color: #000000;
  margin: 2rem 0 0 0;
  font-weight: 700;
  font-size: 2.2rem;
}

.formulario legend {
  font-size: 2rem;
  color: #333333;
  margin: 0;
  margin-top: 3rem;
}
.formulario p {
  font-size: 1.4rem;
  margin: 0;
}
.formulario label {
  text-transform: uppercase;
  font-weight: 900;
  display: block;
  padding: 0;
  margin: 0;
}
.formulario input:not([type=submit]),
.formulario select, .formulario textarea {
  background-color: #FFFFFF;
  padding: 1rem;
  margin-bottom: 2rem;
  display: block;
  width: 100%;
  border: #e1e1e1 solid 1px;
  border-radius: 1rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .form-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario .form-contacto label {
  margin: 1rem 0 1rem 0;
}
.formulario .marginForm {
  margin-top: 1rem;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.alerta {
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
  padding: 0.5rem;
  margin: 1rem 0;
  font-weight: bold;
}
.alerta.error {
  background-color: #c52727;
}
.alerta.correcto {
  background-color: #71B100;
}
.alerta.actualizado {
  background-color: #E08709;
}

.div {
  overflow-x: auto;
}

table {
  margin: 2rem auto 7rem auto;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}
table thead {
  background-color: #71B100;
}
table thead th {
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 2rem;
}
table tbody {
  margin-top: 2rem;
}
table tbody .casa-img {
  margin: auto;
  width: 10rem;
}
table tbody td {
  padding: 2rem;
  text-align: center;
  border-bottom: 1px #e1e1e1 solid;
}

.imagen-small {
  width: 20rem;
  margin-bottom: 2rem;
}

.w-100 {
  width: 100%;
}